<template>
  <div>
    <common-list
      ref="ggcommonlist"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >
      <div v-for="(item,index) of detailList" :key="index" class="order-item">
        <div class="order-loop">
          <!-- <div class="order-tit">
            <div class="left">{{ item.gysmc }}</div>
          </div> -->
          <div class="order-con">
            <div class="goods-img" @click="imgPreview(item.img?item.img:defaultImg)"><img :src="item.img" :onerror="defaultImg"></div>
            <div class="goods-name">
              <div class="name">{{ item.product_name }}</div>
              <van-tag plain type="primary">{{ item.zt_value }}</van-tag>
            </div>
            <!-- <div class="goods-price">
              <div class="jiage">¥{{ item.order_amount }}</div>
            </div> -->
          </div>
          <div class="order-foot">
            <van-button v-if="item.zt<3" plain size="small" @click="confirmShwc(item.id)">完成</van-button>
            <van-button v-if="item.zt<3" plain size="small" @click="confirmQx(item.id)">取消</van-button>
            <van-button plain size="small" @click="goServicesRecode(item.id)">查看</van-button>
          </div>
        </div>
      </div>
    </common-list>
  </div>
</template>

<script land="script" scoped>
import { pageRouter } from '@/common/js/mixin.js'
import { getServices } from '@/store/module.js'
import commonList from '@/components/list/commonList1.vue'
import { ImagePreview } from 'vant'
export default {
  name: 'OrderServiceShsq',
  components: {
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      active: 0,
      checked: true,
      detailList: [],
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"'
    }
  },
  created() {
    this.init()
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    goServicesRecode(id) {
      this.$store.commit('member/SET_ORDERID', id)
      this.$router.push({
        name: 'OrderServiceView'
      })
    },
    confirmQx(id) {
      this.$dialog.confirm({
        title: '提示',
        message: '确认取消售后申请？'
      }).then(() => {
        this.$store
          .dispatch('member/confirmCancelAction', { e_aftersale_id: id })
          .then(res => {
            this.$toast.success('取消成功！')
            this.$bus.$emit('refList1')
          })
          .catch(() => {
            this.loading = false
          })
      }).catch(() => {
      })
    },
    confirmShwc(id) {
      this.$dialog.confirm({
        title: '提示',
        message: '确认售后完成？'
      }).then(() => {
        this.$store
          .dispatch('member/confirmAftersaleAction', { e_aftersale_id: id })
          .then(res => {
            this.$toast.success('确认完成成功！')
            this.$bus.$emit('refList1')
          })
          .catch(() => {
            this.loading = false
          })
      }).catch(() => {
      })
    },
    getPrizeLog(data) {
      this.detailList = data
    },
    init() {
      this.list.postUrl = getServices
      this.list.reqData = { pageno: 1,
        pagerow: 10
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无数据'
    },
    goorderserviceapply() {
      this.$router.push({
        name: 'OrderServiceApply'
      })
    },
    goorderserviceview() {
      this.$router.push({
        name: 'OrderServiceView'
      })
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
