<template>
  <div>
    <!-- <van-image-preview v-model="isShow" :images="slide" /> -->

    <common-list
      ref="ggcommonlist"
      class="common-list"
      :post-url="list.postUrl"
      :req-data="list.reqData"
      :data-name="list.dataName"
      @returnData="getPrizeLog($event)"
    >
      <div v-for="(item,index) of detailList" :key="index" class="order-item">
        <div class="order-loop">
          <div class="order-tit">
            <div class="left">{{ item.gysmc }}</div>
          </div>
          <div class="order-con">
            <div class="goods-img" @click="imgPreview(item.img?item.img:defaultImg)"><img :src="item.img" :onerror="defaultImg"></div>
            <div class="goods-name">
              <div class="name" @click="toOrderServeiceDetail(item)">{{ item.product_name }}</div>
              <van-tag plain type="primary">{{ item.sourceName }}</van-tag>
            </div>
            <div class="goods-price">
              <div class="jiage">¥{{ item.order_amount }}</div>
            </div>
          </div>
          <div class="order-foot">
            <van-button plain size="small" @click="goorderserviceapply(item.id)">申请售后</van-button>
          </div>
        </div>
      </div>
    </common-list>
  </div>
</template>

<script land="script" scoped>
import { pageRouter } from '@/common/js/mixin.js'
import { ImagePreview } from 'vant'
import { myAfsOrder } from '@/store/module.js'
import commonList from '@/components/list/commonList1.vue'
export default {
  name: 'OrderServiceShsq',
  components: {
    commonList
  },
  mixins: [pageRouter],
  data() {
    return {
      list: {
        items: [],
        error: false, // 是否在刷新
        postUrl: '',
        reqData: '',
        dataName: '',
        noDataText: ''
      },
      active: 0,
      checked: true,
      detailList: [],
      defaultImg: 'this.src="' + require('@/assets/no-img.png') + '"'
    }
  },
  created() {
    this.init()
  },
  methods: {
    imgPreview(item) {
      const slide = []
      slide.push(item)
      ImagePreview({
        images: slide, // 预览图片的那个数组
        loop: false
      })
    },
    toOrderServeiceDetail(item) {
      this.$router.push({
        name: 'OrderServiceDetail',
        params: {
          spxx: item
        }
      })
    },
    getPrizeLog(data) {
      this.detailList = data
    },
    init() {
      this.list.postUrl = myAfsOrder
      this.list.reqData = { pageno: 1,
        pagerow: 10
      }
      this.list.dataName = 'list'
      this.list.noDataText = '暂无数据'
    },
    goorderserviceapply(id) {
      this.$router.push({
        name: 'OrderServiceApply',
        params: {
          id: id
        }
      })
    },
    goorderserviceview() {
      this.$router.push({
        name: 'OrderServiceView'
      })
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
