<template>
  <div>
    <headerBar :header-data="headerData" />

    <div class="container-tab">
      <van-sticky class="tabs-sticky">
        <van-tabs v-model="active" swipeable animated sticky>
          <van-tab title="售后服务申请">
            <shsq />
          </van-tab>
          <van-tab title="售后服务记录">
            <sqjl />
          </van-tab>
        </van-tabs>
      </van-sticky>
    </div>
    <div class="common-space" />
    <Backtop class="scrollTop-bar" />
  </div>
</template>

<script land="script" scoped>
import shsq from '@/components/layout/mall/order/orderService/OrderServiceShsq'
import sqjl from '@/components/layout/mall/order/orderService/OrderServiceSqjl'
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter } from '@/common/js/mixin.js'
import Backtop from '@/components/backtop/backtop.vue' // 返回顶部
import { getServices, myAfsOrder } from '@/store/module.js'
export default {
  name: 'OrderServiceList',
  components: {
    headerBar,
    shsq,
    sqjl,
    Backtop
  },
  mixins: [pageRouter],
  data() {
    return {
      active: 0,
      checked: true,
      headerData: {
        title: '售后服务',
        leftUrl: 'MallMine',
        leftText: ' '
      }
    }
  },
  created() {
  },
  methods: {
    // 获取售后服务记录
    getServices() {
      const pra = {
        pageno: 1,
        pagerow: 1000,
        order_name_query: '',
        order_id_query: '',
        afsserviceid_query: ''
      }
      getServices.data = pra
      this.asyncApiModule(getServices).then(res => {
        this.servicesList = res.list
      })
        .catch(() => {
          this.loading = false
        })
    },
    // 获取售后服务申请
    getDefault() {
      const pra = {
        pageno: 1,
        pagerow: 1000
      }
      myAfsOrder.data = pra
      this.asyncApiModule(myAfsOrder).then(res => {
        this.detailList = res.list
      })
        .catch(() => {
          this.loading = false
        })
    },
    goBack() {
      this.$router.back()
    },
    goorderserviceapply() {
      this.$router.push({
        name: 'OrderServiceApply'
      })
    },
    goorderserviceview() {
      this.$router.push({
        name: 'OrderServiceView'
      })
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
